.footer {
  width: 95%;
  max-width: 1298px;
  margin: 10px auto;
  padding: 10px 10px;

  border-radius: 30px;

  background-color: #03080f;

  font-family: "Handjet", sans-serif;
  font-optical-sizing: auto;
  font-variation-settings: "ELGR" 1, "ELSH" 2;
  font-size: 30px;
  text-align: center;
}

@media (max-width: 840px) {
  .footer {
    font-size: 26px;
  }
}

@media (max-width: 615px) {
  .footer {
    font-size: 18px;
  }
}


