.photo_field_box {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;

  width: 95%;
  max-width: 1298px;
  margin: 20px auto 10px;
  padding: 30px 10px;

  border-radius: 30px;
}
.linksYouTube {
  width: 45%;

  margin: 5px;
  padding: 15px;

  border-radius: 10px;
  background-color: #03080f;
  box-shadow: 0 0 50px #262a2d;
  cursor: pointer;
}
