.canvas {
  max-width: 469px;
  max-height: 469px;
  width: auto;
  height: 100%;

  margin: 15px;

  border-radius: 20px;
  border: 1px solid pink;
}

.canvas:hover {
  transform: scale(1.2);
  transition: all 0.3s;
}

@media (max-width: 590px) {
  .canvas {
    max-width: 420px;
    max-height: 420px;
  }

  .canvas:hover {
    transform: scale(1);
  }
}

@media (max-width: 480px) {
  .canvas {
    max-width: 390px;
    max-height: 390px;
  }
}

@media (max-width: 440px) {
  .canvas {
    max-width: 300px;
    max-height: 300px;
  }
}

@media (max-width: 340px) {
  .canvas {
    max-width: 280px;
    max-height: 280px;
  }
}
