.box__title {
  width: 95%;
  max-width: 1298px;
  margin: 10px auto;
  padding: 25px 10px;

  border-radius: 30px;

  text-align: center;

  background-repeat: no-repeat;
  background-size: cover;
}

.title {
  font-family: "Great Vibes", cursive;
  font-weight: 400;
  font-style: normal;
  font-size: 110px;

  color: white;
  text-shadow: 4px 4px 4px #000;
}

 @media (max-width: 777px) {
  .box__title {
    padding: 20px 7px;
  } 
  .title {
    font-size: 80px;
  }
}

@media (max-width: 580px) {
  .box__title {
    padding: 18px 5px;
  }
  .title {
    font-size: 60px;
  }
} 

@media (max-width: 444px) {
  .box__title {
    padding: 15px 4px;
  }
  .title {
    font-size: 50px;
  }
} 

@media (max-width: 350px) {
  .title {
    font-size: 45px;
  }
} 