.filters_box {
  width: 95%;
  max-width: 1298px;
  margin: 40px auto;
  padding: 10px 10px;

  border-radius: 30px;

  color: white;
  background-color: #03080f;

  font-family: "Handjet", sans-serif;
  font-optical-sizing: auto;
  font-variation-settings: "ELGR" 1, "ELSH" 2;
}

.title {
  margin: 10px 15px 6px;

  text-align: center;
  font-weight: 700;
  font-size: 28px;
}

.ul_nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 0 -13px 15px;

  list-style: none;

  font-size: 24px;
  font-weight: 400;
}

.nav__li {
  margin-right: 15px;
  margin-bottom: 6px;

  cursor: pointer;
}

.nav__li:hover {
  color: pink;
}

.nav__li_red {
  color: #739ca5;
  border-bottom: solid 1px #739ca5;
}

.span_dekor_t,
.span_dekor_m,
.span_dekor_b {
  display: block;
  text-align: center;
  color: green;

  pointer-events: none;
}

.span_dekor_m,
.span_dekor_b {
  margin-top: -12px;
}

.select_nav {
  display: block;
  width: 90%;

  margin: 5px auto;
  padding: 10px;

  font-size: 26px;

  border: none;
  border: 1px solid #739ca5;
  border-radius: 12px;

  color: #fff;

  background-color: #03080f;

  font-family: "Handjet", sans-serif;
  font-optical-sizing: auto;
  font-variation-settings: "ELGR" 1, "ELSH" 2;

  -webkit-appearance: none; /* Убирает стрелочку в браузерах на основе WebKit (Chrome, Safari) */
  -moz-appearance: none; /* Убирает стрелочку в Firefox */
  appearance: none;

  cursor: pointer;
}

.span_new {
  margin-left: 6%;
  font-size: 20px;
  color: yellow;
  cursor: pointer;
}

@media (max-width: 760px) {
  .filters_box {
    margin: 20px auto;
  }
}

@media (max-width: 615px) {
  .title {
    font-size: 24px;
  }
  .ul_nav {
    font-size: 20px;
  }
  .select_nav {
    font-size: 22px;
  }
  .span_new {
    font-size: 16px;
  }
}
