@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap");
.box__text {
  width: 95%;
  max-width: 1298px;
  margin: -5px auto 10px;
  padding: 13px 10px;

  font-family: "Caveat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
  font-size: 28px;

  color: #0f0fa7;
}

.accordion_btn {
  display: block;
  width: 100%;
  max-width: 170px;
  margin: 40px auto 0;
  padding: 10px;

  font-size: 18px;

  font-family: "Roboto Condensed", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;

  border-radius: 20px;
  box-shadow: inset 0 0 15px black;
  color: #000;
  background-color: red;
  transition: all 0.1s;
  cursor: pointer;
}

.accordion_btn:hover {
  color: #ffffff;
  box-shadow: inset 0 0 15px white;
}

.accordion_btn:active {
  color: #424242;
  background-color: rgb(81, 8, 8);
  box-shadow: inset 0 0 15px rgb(65, 65, 65);
}

.accordion_content {
  max-height: 0;
  overflow: hidden;
}

.accordion_open {
  max-height: 100%; /* Укажите высоту в зависимости от контента */
}

.item_text_1 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0 auto 10px;

  width: 100%;
  height: 100%;
  max-width: 684px;
  min-height: 274px;

  transform: rotate(-5deg);
}

.text_1 {
  margin: 46px 0 0px;
  padding: 40px 93px 0 93px;
}

.item_text_2 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0px auto 10px;

  width: 100%;
  height: 100%;
  max-width: 428px;
  min-height: 270px;
}

.text_2 {
  margin: -124px 0 0px;
  padding: 121px 20px 10px 24px;
}

.item_text_3 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0px auto 10px;

  width: 100%;
  height: 100%;
  max-width: 312.5px;
  min-height: 600.5px;
}

.text_3 {
  margin: -82px 0 0px;
  padding: 113px 21px 0 25px;
}

.item_text_4 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0px auto 10px;
  width: 100%;
  height: 100%;
  max-width: 343px;
  min-height: 202px;

  transform: rotate(-3deg);
}

.text_4 {
  margin: -282px 0 0px;
  padding: 64px 9px 0 20px;
}

.item_text_5 {
  background-repeat: no-repeat;
  background-size: cover;

  margin: 0px auto 20px;
  width: 100%;
  height: 100%;
  max-width: 555px;
  min-height: 248px;

  transform: rotate(-1deg);
}

.text_5 {
  margin: -56px 0 0px;
  padding: 40px 60px 0 82px;
}

.span_dekor {
  display: block;
  width: 100%;
  color: #739ca5;
  text-align: center;
}

span {
  color: red;
}

.span_dekor_top,
.span_dekor_bot {
  display: block;

  margin: 0 auto;
  color: #739ca5;
  text-align: center;

  pointer-events: none;
}

@media (max-width: 760px) {
  .box__text {
    font-size: 22px;
  }

  .item_text_1 {
    max-width: 548px;
    min-height: 192px;
  }

  .text_1 {
    margin: 20px 0 0px;
    padding: 26px 76px 0 76px;
  }

  .text_2 {
    padding: 128px 20px 5px 22px;
  }

  .item_text_3 {
    max-width: 297.5px;
    min-height: 574.5px;
  }

  .text_4 {
    margin: -324px 0 0px;
  }

  .item_text_5 {
    max-width: 505px;
    min-height: 194px;
  }

  .text_5 {
    margin: -102px 0 0px;
  }
}

@media (max-width: 615px) {
  .box__text {
    display: none;
  }
}
